import { _decorator, Component, Label, Node, tween, v3} from 'cc';
const { ccclass, property } = _decorator;

@ccclass('GameRoot')
export class GameRoot extends Component {
    @property(Node) button: Node = null;
    @property(Node) wxy: Node = null;
    @property(Node) rLabelNode: Node = null;

    start() {
        this.button.on(Node.EventType.TOUCH_START, () => {
            this.button.setScale(1.2, 1.2, 1.2);
        });

        this.button.on(Node.EventType.TOUCH_END, () => {
            this.myAnimation();
            this.button.setScale(1, 1, 1);

        });

    }

    update(deltaTime: number) {
        
    }


    myAnimation() {

        const obj = {
            n: 0
        }
        const comp = this.rLabelNode.getComponent(Label);

        tween(obj)
        .to(3, {n: 100}, {onUpdate(target, ratio) {
            comp.string = String(obj.n)
            this.wxy.setPosition(v3(0, obj.n, 0));
        },
        easing: 'elasticOut'}) // 缓动曲线
        .start();
    }
}

